        methods: {

            check_status(){

                let that = this
                this.int_check = setInterval(function () {
                     that.process -= 10
//                    console.log(that.process)
                    if(that.process <= 0){
                        that.process = 100
//                        console.log('SOCKET_CLIENT.data.wSock=>')
//                        console.log(SOCKET_CLIENT.data.wSock)
                        if(SOCKET_CLIENT.data.wSock){

                        }else{
                            that.me.me_status = false
                        }
                    }
                 },1000)
            },
            stop_check_status(){
                window.clearInterval(this.int_check)
            },
            socket_init()  {
                SOCKET_CLIENT.data.this_vue = this

                SOCKET_CLIENT.init()
            },
            disconnect() {
                SOCKET_CLIENT.data.this_vue = this

                SOCKET_CLIENT.wsClose()
            },
            reconnect() {

            },
            send(){

                this.payload = {
                    to:parseInt(this.to),
                    payload_type:this.payload_type,
                    payload_data:this.payload_data,
                    yaf:{
                        module:this.module,
                        controller:this.controller,
                        action:this.action,
                    }
                }
                this.payload['cmd'] = this.cmd
                SOCKET_CLIENT.data.payload = this.payload
                SOCKET_CLIENT.data.this_vue = this
                let response = SOCKET_CLIENT.wsSend()

            }


        },

    }



